* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

p, h2, h3 {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  min-height: 408px;
  padding: 0 16px;
  display: flex;
  overflow: hidden;
}

.card__top {
  margin: 32px auto 40px;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
  top: 0;
  left: 0;
}

.card__image > img {
  object-fit: contain;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.card__price-name {
  color: #616070;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-shadow: none;
  cursor: pointer;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: #00acdc;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  display: inline-flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.feedbacks {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.feedbacks__review {
  align-items: end;
  margin-bottom: -1px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.stars {
  flex-direction: row;
